@font-face {
  font-family: "lofi";
  src: local("lofi"),
    url("../fonts/lofi/lofi.ttf") format("truetype");
  font-weight: bolder;
}

body {
  font-family: 'lofi', monospace;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-family: 'lofi', monospace;
  font-weight: bolder;
  background-color: #00224B;
  border-radius: 10px;
  width: fit-content;
  border-style: solid;
  border-color: #CEBDF1;
  color: #CEBDF1;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

button:hover {
  background-color: #CEBDF1;
  color: black;
}

.header {
  margin-right: 15px;
  margin-left: 15px;
  padding: 10px;
  color: #CEBDF1;
  font-size: 2rem;
  font-weight: bolder;
}

.filler-block {
  background-color: #00224B;
  height: 10vh;
  width: 100vw;
}